html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent}body{line-height:1}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}nav ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent}ins{background-color:#ff9;color:#000;text-decoration:none}mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}del{text-decoration:line-through}abbr[title],dfn[title]{border-bottom:1px dotted;cursor:help}table{border-collapse:collapse;border-spacing:0}hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}input,select{vertical-align:middle}body{background:#f3d7dc;font-family:'Press Start 2P', cursive}.main-container{width:100%;display:flex;flex-direction:column;align-items:center;margin-top:2vh}.game-header{padding:4vh;margin-bottom:2vh;font-size:45px;color:white;text-shadow:-4px 0 black, 0 4px black, 4px 0 black, 0 -4px black;background:maroon;box-shadow:3px 3px black}@-webkit-keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}@-moz-keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}@-ms-keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}@-o-keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}@keyframes color-change{0%{text-shadow:4px 4px #e75959;color:#e75959}50%{text-shadow:4px 4px gold;color:gold}100%{text-shadow:4px 4px #e75959;color:#e75959}}.game-panel{display:flex}.game-panel section{padding:0 1rem;padding-top:10vw}.game-panel section h1{background:maroon;color:white;padding:10px;text-shadow:-2px 0 black, 0 2px black, 2px 0 black, 0 -2px black}.game-panel section p{padding:6px;padding-top:10px;background:white}.game-panel section:first-child{text-align:right}.game-panel section:second-child{text-align:left}#gameWindow{width:60vw;border:30px inset #FFF}
